import React from 'react';
import logo from '../../assets/images/2gather.jpg';
import AuthController from '../../platform/api/auth';
import './style.scss';


class SignIn extends React.Component {
    state = {
        form: {
          firstName: '',
          lastName: '',
          email: '',
          subject: '',
          message: '',
        },

        password: false,
        focused: false,
        submited: false,
      };

     validateEmail = (email) => {
        var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email);
      }
    
      change = e => {
        const { form } = this.state;
        form[e.target.name] = e.target.value;
        this.setState({ form });
      }
    
      send = e => {
        e.preventDefault();
        const { submited } = this.state;
        if (!submited) this.setState({ submited: true }, this.checkForSend);
        else this.checkForSend();
      }

      checkForSend = () => {
        const { form } = this.state;
    
        if (form.firstName && form.lastName &&
          isValidEmail(form.email) &&
          form.subject && form.message) this.setState({ submitLoading: true }, async () => {
          const result = await ContactController.Send(form);
          if (result.Success) {
            NotificationManager.success('Message successfully sent');
            window.routerHistory.push(ROUTES.HOME);
          } else NotificationManager.error('Failed to send the message');
          this.setState({ submitLoading: false });
        });
      }
    

    togglePassword = () => {
        this.setState({
            password: !this.state.password,
        })
    }


    focusHandler = (e) => {
        console.log(e, e.target)
        this.setState({
            focused: true,
        })
    }

    blurHandler = () => {
        console.log('focused')
        this.setState({
            focused: false,
        })
    }



    render(){
        const { password, focused } = this.state;
        return (
            <div className="sign-in">
                <div className="sign-in-form">
                    <div className="sign-in-form-container">
                        <h2>Sign in</h2>
                        <p>Enter your email and password and login in your <br/> account</p>
                        <form>
                            <div className="G-field">
                                <label className={`G-form-label  ${focused ? 'G-label-animated' : ''}`} htmlFor="signin-email">Email</label>
                                <input type="email" onFocus={this.focusHandler} onBlur={this.blurHandler}/>
                               
                                <span className={`input-border ${focused ? "input-border-animated" : "" }`}></span>
                            </div>
                            <div className="G-field">
                                <label className={`G-form-label  ${focused ? 'G-label-animated' : ''}`} htmlFor="signin-password">Password</label>
                                 <input type={!password ? "password" : "text"} onFocus={this.focusHandler} onBlur={this.blurHandler}/>
                                
                                 <span className={`input-border ${focused ? "input-border-animated" : "" }`}></span>
                                <i className={!password ? "icon-ic_eye" : "icon-ic_eyeclosed"} 
                                onClick={this.togglePassword}></i>
                            </div>
            
                            <button className="G-button" onClick={this.handleSignIn}>
                                Sign in
                            </button>
                        </form>
                    </div>
                </div>
                <div className="logo">
                    <img src={logo} alt="2gather"/>
                </div>
            </div>
        )
    }
}

export default SignIn;